import s from "./Banner.module.scss";
import Img1 from "../../../assets/images/C1.png";
import Img2 from "../../../assets/images/C5(2).png";

const Banner = () => {
  return (
    <div className={s.wrapperBanner}>
      <div className={s.imgWrapper}>
        <img className={s.img} src={Img1} alt="" />
        <img className={s.img} src={Img2} alt="" />
      </div>
      <h2 className={s.title}>CКИДКИ на все запчасти до 70%</h2>
      <button>ПОСМОТЕТЬ ВСЕ</button>
    </div>
  );
};

export default Banner;
